import moment from 'moment'

import {notStarted, inProgress, finished} from './constants'

export default class Task {
    constructor(task) {
        this.startT = (task.start)  ? moment(task.start) : null
        this.stopT = (task.stop)  ? moment(task.stop) : null

        if (task.start && !task.stop) { this.status = inProgress }

        if (task.start && task.stop) { this.status = finished }
    }

    start() {
        if (!this.startT) {
            this.startT = moment().toDate()
        }
    }

    stop() {
        if (!this.stopT) {
            this.stopT = moment().toDate()
        }
    }

    getTime() {
        if (this.stop) {
            return this.stop.diff(this.startT, 'seconds')
        } else {
            return moment().diff(this.startT, 'seconds')
        }
    }
}
